.HeroSection{
    width: 100vw;
    height: 80vh;
    background-color: #2155CD;
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: flex-start;
    transition: all;
    overflow: auto;
}

.ContentSection{
    align-self: center;
    width: 40%;
    height: 500px;
    /* background-color: wheat; */
    display: flex;
    text-align: start;
    color: white;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    /* justify-content: center; */
    padding-top: 220px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 100px;
    /* align-items: center; */
    font-size: 2rem;
}

.JoinUS{
    background-color: whitesmoke;
    color: #151517;
    border: 2px solid black;
    border-radius: 10px;
    padding: 7px 60px;
    cursor: pointer;
    font-size: 2.5rem;
}

.EventsSection{
    align-self: center;
    width: 50%;
    height: 500px;
    position: relative;
    /* background-color: burlywood; */
    /* display: flex; */
    border-radius: 10px;
    justify-content: center;
    /*align-items: center; */
    font-size: 4rem;
    /* border: 5px solid red; */
}

.InactiveLeft{
    top: 95px;
    left: 50%;
    transform: translatex(-120%);
    position: absolute;
    width: 240px;
    height: 260px;
    background-color: whitesmoke;
    border-radius: 20px;
    border: 2px solid #2155CD;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Active{
    top: 80px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    width: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0.2px 0.2px;
    height: 260px;
    z-index: 2000;
    border-radius: 20px;
    border: 2px solid #2155CD;
    background-color: whitesmoke;
}

.InactiveRight{
    top: 95px;
    left: 50%;
    transform: translateX(20%);
    position: absolute;
    width: 240px;
    height: 260px;
    border-radius: 20px;
    border: 2px solid #2155CD;
    background-color: whitesmoke;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ImageTitle{
    position: absolute;
    top: 400px;
    left: 50%;
    width: 90%;
    text-align: center;
    color: whitesmoke;
    transform: translateX(-50%);
    font-size: 2.4rem;
    /* border: 2px solid black; */
    font-weight: 600;
}

@media (max-width: 898px) {
    .HeroSection{
        flex-direction: column-reverse;
        overflow: scroll;
        height: 120vh;
    }

    .ContentSection{
        width: 80%;
        height: 40%;
        margin-bottom: 10px;
        align-items: center;
        padding-top: 30px;
    }
    p{
        text-align: center;
    }

    .EventsSection{
        margin-top: 100px;
        height: 60%;
        width: 80%;
        
    }
}